<template>
    <div>
        <el-table highlight-current-row :data="coupons">
            <el-table-column prop="index" label="#" width="50"/>
            <el-table-column prop="code" label="" min-width="80"/>
            <el-table-column prop="title" label="" min-width="100"/>
            <el-table-column prop="type" label="" min-width="120"/>
            <el-table-column prop="content" label="" min-width="120"/>
            <el-table-column prop="publish" label="" min-width="90"/>
            <el-table-column prop="beginToEnd" label="" min-width="80"/>
            <el-table-column prop="activeTime" label="" min-width="100"/>
            <el-table-column prop="issuedNum" label="" min-width="80"/>
            <el-table-column prop="stockNum" label="" min-width="100"/>
            <el-table-column prop="receivedNum" label="" min-width="100"/>
            <el-table-column prop="usedNum" label="" min-width="100"/>
            <el-table-column prop="usedRate" label="" min-width="100"/>
            <el-table-column label="" width="250">
                <template slot-scope="scope">
                    <el-button type="success" size="mini" :disabled="true" @click="show(scope.row)"></el-button>
                    <el-button type="info" size="mini" :disabled="true" v-if="scope.row.status === 2"></el-button>
                    <el-button type="warning" size="mini" v-else-if="scope.row.status !== 0"
                               :disabled="scope.row.status === 3" @click="disabled(scope.row)">{{
                        scope.row.status === 3 ? "": ""
                        }}
                    </el-button>
                    <el-button type="primary" size="mini" v-if="scope.row.status === 1"
                               @click="promoteQrCode(scope.row)">
                    </el-button>
                    <el-button type="primary" size="mini" @click="switchToTemplateSet(scope.row)"></el-button>
                </template>
            </el-table-column>
        </el-table>

        <el-dialog title="" width="25%" :visible.sync="qrcodeDialogVisible">
            <el-tabs type="border-card">
                <el-tab-pane label="">
                    <div class="qrcode-img" v-if="ticketPromote&&ticketPromote.promoteUrl">
                        <img width="200px" :src="ticketPromote.promoteUrl" alt="">
                    </div>
                    <div class="qrcode-img" v-else></div>
                </el-tab-pane>
                <el-tab-pane label="">
                    <div class="qrcode-img" v-if="ticketPromote&&ticketPromote.promoteMiniCodeUrl">
                        <img width="200px" :src="ticketPromote.promoteMiniCodeUrl" alt="">
                    </div>
                    <div class="qrcode-img" v-else></div>
                </el-tab-pane>
            </el-tabs>
        </el-dialog>
    </div>
</template>
<script>
    export default {
        props: {
            coupons: {
                default: null,
                type: Array
            }
        },
        data() {
            return {
                qrcodeDialogVisible: false,
                ticketPromote: null,
            };
        },
        methods: {
            show(row) {
                console.log(row);
            },
            async disabled(row) {
                let result = this.http.tickets.disabled(this.$requestInput('projectId'), row.id);
                if (result) {
                    this.$message.success('');
                    this.$command('DATA_LIST', 'http.couponCards', 'couponCards/setList', this.$requestInput('page'));
                }
            },
            promoteQrCode(ticket) {
                this.qrcodeDialogVisible = true;
                this.ticketPromote = ticket;
            },
            switchToTemplateSet(ticket) {
                this.$command('CREATE_UPDATE_ENTITY_INIT', 'couponCards/createEditInit', ticket);
                this.$router.push({
                    name: 'coupon-bind-template',
                    params: {
                        ticketId: ticket.id
                    }
                });
            }
        }
    }
</script>

<style scoped="scoped">
    .qrcode-img {
        text-align: center;
    }
</style>
